import { useDeviceInfo } from "@rp/react/web/common/hooks"
import AppStoreLogo from "./assets/appstore.svg"
import GooglePlayLogo from "./assets/googleplay.svg"

type Props = { linkToApp: string; isAvailable: boolean; storeType: string }

export function AppStoreButton({ linkToApp, isAvailable, storeType = "google" }: Props) {
  const { isMobile } = useDeviceInfo()

  return isAvailable ? (
    <a target="_blank" rel="noopener noreferrer" href={linkToApp} style={{ color: "inherit" }}>
      {storeType.toLocaleLowerCase() === "google" ? (
        <GooglePlayLogo width={isMobile ? "140px" : "180px"} height={isMobile ? "42px" : "52px"} />
      ) : (
        <AppStoreLogo width={isMobile ? "140px" : "180px"} height={isMobile ? "42px" : "52px"} />
      )}
    </a>
  ) : storeType.toLocaleLowerCase() === "google" ? (
    <GooglePlayLogo width={isMobile ? "140px" : "180px"} height={isMobile ? "42px" : "52px"} />
  ) : (
    <AppStoreLogo width={isMobile ? "140px" : "180px"} height={isMobile ? "42px" : "52px"} />
  )
}

